import React from "react";
import moment from "moment";
import {
  Card,
  CardActions,
  CardContent,
  Grid,
  Typography,
} from "@material-ui/core";
import ResultInput from "./result-input";

function BetCard({ bet, onChange }) {
  const kickoff = moment(bet.kickoff);
  const started = moment().isAfter(kickoff);
  return (
    <Card
      variant="outlined"
      style={{
        marginBottom: "15px",
        backgroundColor: bet.isFinished ? "#b9b9b9" : "initial",
        width: "500px",
      }}
    >
      <CardContent>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="baseline"
        >
          <Grid item>
            <Typography variant="body1" component="div">
              <span>{kickoff.format("dddd MM/DD")}</span>
            </Typography>
          </Grid>
          <Grid item>
            <Typography
              color="textSecondary"
              display={"inline"}
              variant={"alignRight"}
            >
              <span>{bet.group.replace("Group", "Grupp")}</span>
            </Typography>
          </Grid>
        </Grid>

        <CardActions>
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            spacing={2}
          >
            <Grid item>
              <ResultInput
                teamName={bet.home.team}
                onChangeResult={(val) => {
                  bet.home.result = val;
                  onChange(bet);
                }}
                initialValue={bet.home.result}
                disabled={started}
              />
            </Grid>
            <Grid item>
              <Typography variant="body1" component="div">
                <span>{kickoff.format("HH:mm")}</span>
              </Typography>
            </Grid>
            <Grid item>
              <ResultInput
                teamName={bet.away.team}
                onChangeResult={(val) => {
                  bet.away.result = val;
                  onChange(bet);
                }}
                initialValue={bet.away.result}
                disabled={started}
              />
            </Grid>
          </Grid>
        </CardActions>
      </CardContent>
    </Card>
  );
}

export default BetCard;
